import { Component, OnInit } from '@angular/core';
import { ClienteService } from '../services/cliente-service/cliente.service';
import { configHelper } from '../configHelper';

@Component({
  selector: 'app-clientes',
  templateUrl: './clientes.page.html',
  styleUrls: ['./clientes.page.scss'],
})
export class ClientesPage implements OnInit {

  constructor(private clienteSrvc: ClienteService) { }

  ngOnInit() {


    let userData = JSON.parse(localStorage.getItem(configHelper.storageKeys.user));
    console.log('dados do usuario', userData)

    this.clienteSrvc.getClientsByIdUser(userData._id).subscribe(v => {
      console.log('clientes', v)
      // this.ngxService.stop();
    })
  }

}
